import axios from 'axios';
import authHeader from './auth-header';
import urlService from './url-service';

class FormService {
    submit(path,data,files){
        let hasFile = files.length>0
        var form_data = new FormData();

        if (hasFile){
            for ( var key in data ) {
                form_data.append(key, data[key]);
            }
            for (var i = 0; i < files.length ; i++){
                form_data.append(files[i].key, files[i].content)
            }
            console.log(form_data)
            console.log(hasFile)
        }
        
        return axios.post(urlService('API_URL')+path,
                        hasFile?form_data:JSON.stringify(data),
                            { headers: authHeader(hasFile) }
                         )
    }
    loadAutoComplete(path,value){
        return axios.get(urlService('API_URL')+path+value,
                            { headers: authHeader(false) }
                        )
    }
}

export default new FormService();
