import DatatableService from '../services/datatable.service'

export const datatable = {
    namespaced: true,
    state: {
        table : null,
        message : null,
        dialog : null,
        object : null
    },
    actions :{
        loadDialog({ commit }, payload){
            return DatatableService.loadDialog(payload.actionType,payload.id).then(
              dialog => {
                commit('dialogSuccess', dialog);
                return Promise.resolve(dialog);
              },
              error => {
                commit('dialogFailure');
                return Promise.reject(error);
              }
            )
        },
        loadObject({ commit }, payload){
            return DatatableService.loadObject(payload.actionUrl,payload.id).then(
              object => {
                commit('objectSuccess', object);
                return Promise.resolve(object);
              },
              error => {
                commit('objectFailure');
                return Promise.reject(error);
              }
            )
        },
        loadTable({ commit }, payload){
          return DatatableService.loadTable(payload.path,payload.data).then(
            table => {
              commit('tableSuccess', table);
              return Promise.resolve(table);
            },
            error => {
              commit('tableFailure');
              return Promise.reject(error);
            }
          )
      },
    },
    mutations: {
        dialogSuccess(state, dialog) {
           state.dialog = dialog;
        },
        dialogFailure(state) {
            state.dialog = null
        },
        objectSuccess(state, object) {
            state.object = object;
         },
        objectFailure(state) {
            state.object = null
        },
        tableSuccess(state, table) {
            state.table = table;
        },
        tableFailure(state) {
            state.table = null
        },
    },

}