import axios from 'axios';
import authHeader from './auth-header';
import urlService from './url-service';

class DatatableService {
    loadDialog(actionType,id){
        return axios.get(urlService('API_URL')+actionType+"-modal-rest/get/"+id, 
                            { headers: authHeader(false) }
                        )
    }
    loadObject(actionUrl,id){
        return axios.get(urlService('API_URL')+actionUrl+id, 
                            { headers: authHeader(false) }
                        )
    }
    loadTable(path,data){
        return axios.post(urlService('API_URL')+path,
                            JSON.stringify(data),
                            { headers: authHeader(false) }
                         )
    }
}

export default new DatatableService();
