export default class currentuser{
  expires_in = null;
  token_type = null;
  refresh_token = null;
  jti = null;
  username = null;
  email = null;
  scope = null;
  accessToken = null;
  company = null;
  businessUnit = null;
  branch = null;
  module = null;
  referenceDate = null;
  language = null;
  constructor(result) {
    this.username = result.username;
    this.email = result.email;
    this.scope = result.scope;
    this.accessToken = result.accessToken;
    this.language = {"id":1}//result.language;
    this.expires_in = result.expires_in;
    this.token_type = result.token_type;
    this.refresh_token = result.refresh_token;
    this.jti = result.jti;
  }

  
}
