export default function authHeader(hasFile) {
  let user = JSON.parse(localStorage.getItem('user'));

  if (user && user.accessToken) {
    return { Authorization: 'Bearer ' + user.accessToken,
            'Content-type': (hasFile?'multipart/form-data':'application/json')}; // for Spring Boot back-end
    // return { 'x-access-token': user.accessToken };       // for Node.js Express back-end
  } else {
    return {'Content-type': (hasFile?'multipart/form-data':'application/json')}; //
  }
}
