import axios from 'axios';
import authHeader from './auth-header';
import urlService from './url-service';

class WorkspaceService {
    open(languageId,workspaceId){
        return axios.get(urlService('API_URL')+"workspace-rest/open/"+languageId+"/"+workspaceId, 
                            { headers: authHeader(false) }
                        )
    }
    loadSideBarMenu(languageId,moduleId){
        return axios.get(urlService('API_URL')+"module-rest/open/"+languageId+"/"+moduleId, 
                            { headers: authHeader(false) }
                        )
    }
}

export default new WorkspaceService();
