import FormService from '../services/form.service'

export const form = {
    namespaced: true,
    state: {
      /*begin - used in Form and Field components */
      datalists: [],
      rules: [],
      item : null,
      buttons : [],
      files : [],
      /*end  - used in Form and Field components */
      form : null,
      autocomplete : null,
    },
    actions :{
        submit({ commit }, payload){
          return FormService.submit(payload.path,payload.data,payload.files).then(
            form => {
              commit('formSuccess', form);
              return Promise.resolve(form);
            },
            error => {
              commit('formFailure');
              return Promise.reject(error);
            }
          )
        },
        loadAutoComplete({commit}, payload){
          return FormService.loadAutoComplete(payload.path,payload.value).then(
            autocomplete => {
              commit('autocompleteSuccess', autocomplete);
              return Promise.resolve(autocomplete);
            },
            error => {
              commit('autocompleteFailure');
              return Promise.reject(error);
            }
          )
        }
    },
    mutations: {
        formSuccess(state, form) {
            state.autocomplete = form;
        },
        formFailure(state) {
            state.form = null
        },
        autocompleteSuccess(state, autocomplete) {
          state.autocomplete = autocomplete;
        },
        autocompleteFailure(state) {
          state.autocomplete = null
        },
    },

}