import Logger from './logger.service'

let URLs = []
URLs.push(['API_URL', 'http://localhost:8080/'])
URLs.push(['AUTHENTICATION_URL', 'http://localhost:9001/oauth/token'])

export default function urlService(name) {
    let position = -1
    for (let i = 0; i < URLs.length; i++){
        if (URLs[i][0]==name){
            position = i
            break
        }
    }

    if (position == -1)
        Logger("[URL-SERVICE] " + name +  " is INVALID")
    else
        return URLs[position][1]
    return ""
}
  