import WorkspaceService from '../services/workspace.service'

export const workspace = {
    namespaced: true,
    state: {
        workspace : null,
        menu : null,
        message : null
    },
    actions :{
       open({ commit }, payload){
            return WorkspaceService.open(payload.languageId, payload.workspaceId).then(
              workspace => {
                commit('workspaceSuccess', workspace);
                return Promise.resolve(workspace);
              },
              error => {
                commit('workspaceFailure');
                return Promise.reject(error);
              }
            )
        },
        loadSideBarMenu({ commit }, payload){
          return WorkspaceService.loadSideBarMenu(payload.languageId,payload.moduleId).then(
            menu => {
              commit('menuSuccess', menu);
              return Promise.resolve(menu);
            },
            error => {
              commit('menuFailure');
              return Promise.reject(error);
            }
          )
      },
    },
    mutations: {
        workspaceSuccess(state, workspace) {
          state.workspace = workspace;
        },
        workspaceFailure(state) {
            state.workspace = null
        },
        menuSuccess(state, menu) {
          state.menu = menu;
        },
        menuFailure(state) {
          state.menu = null
        },
    },

}