import axios from 'axios'
import currentuser from '../models/currentuser'
import urlService from './url-service';

const CLIENT_ID = 'USER_CLIENT_APP'
const CLIENT_SECRET = 'password'

class AuthService {
  login(user) {
    var reqData = "username="+user.username+"&password="+user.password+"&grant_type=password"
    const config = {
      url : urlService('AUTHENTICATION_URL'),
      method: 'POST',
      headers: { 
        "Authorization": "Basic "+window.btoa(CLIENT_ID+ ':' + CLIENT_SECRET),
      }     
    };
    console.log("im here currentuser")
    return axios.post(urlService('AUTHENTICATION_URL'), reqData, config)
          .then(response => {
                let loggedUser = null  
                  if (response.data.access_token) {
                    loggedUser = new currentuser(response.data)
                    localStorage.setItem('user', JSON.stringify(loggedUser));
                  }
                  return loggedUser}
                )
  }
  logout() {
    localStorage.removeItem('user');
  }

  register(user) {
    return axios.post(urlService('AUTHENTICATION_URL') + 'signup', {
      username: user.username,
      email: user.email,
      password: user.password
    });
  }
}

export default new AuthService();
